<template>
	<view class="box">
		<mescroll-body ref="mescrollRef" @init="mescrollInit" @down="downCallback" @up="upCallback">
			<view class="address-li" v-for="(item,k) in data_list" :key="k">
				<view class="address-box" @tap="chooseAddress(k)">
					<view class="title">
						<text class="default font-bold" v-if="item.isDefaultAddress==100">默认</text>
						{{item.address1}} {{item.address2}}
					</view>
					<view class="name-box">
						<view class="name">{{item.consignee}}</view>
						{{item.mobile}}
					</view>
				</view>
				<navigator :url="'./address_add?seqId='+item.seqId" class="iconfont iconbianji-shouhuodizhi"></navigator>
			</view>
		</mescroll-body>
		
		<view class="blank" :class="{'padd-safe':shapedScreen}"></view>
		<navigator url="address_add" class="fix-box" :class="{'padd-safe':shapedScreen}">
			<view class="bg-btn">
				新增收货地址
			</view>
		</navigator>
	</view>
</template>

<script>
	import MescrollMixin from "@/components/mescroll-uni/mescroll-mixins.js";
	import MescrollBody from "@/components/mescroll-uni/mescroll-body.vue"
	let from=""//判断是否从商品购买入口进入
	export default {
		mixins: [MescrollMixin], // 使用mixin
		data() {
			return {
				data_list: [],
				pop_show: false,
				delete_id: 0,
				buy_in:false,
				shapedScreen:this.shapedScreen
			}
		},
		components: {
			MescrollBody
		},
		onLoad(option) {
			from=option.from
		},
		onShow() {
			this.$nextTick(function(){
				this.mescroll.resetUpScroll(true);
				this.mescroll.scrollTo(0, 0)
			})
			
		},
		methods: {
			chooseAddress(k){
				if(!from) return;
				let choose_address=this.data_list[k];
				this.$store.commit('saveAddress',choose_address);
				uni.navigateBack({})
			},
			upCallback(page) {
				let pageNum = page.num; // 页码, 默认从1开始
				let pageSize = page.size; // 页长, 默认每页10条
				this.$api.get('/o2oShippingAddress/findPage',{params:{
					customerSeqId:this.$common.getLocalSync('customerSeqId'),
					pageno:pageNum,
					pagesize:pageSize
				}}).then((res) => {
					if(res.code&&res.code!=200){
						this.mescroll.endErr();
						return}
					this.$store.commit('saveAddress',{});
						let data = res;
						// 接口返回的当前页数据列表 (数组)
						let curPageData = data.list;
						// 接口返回的当前页数据长度 (如列表有26个数据,当前页返回8个,则curPageLen=8)
						let curPageLen = curPageData.length;
						// 接口返回的总页数 (如列表有26个数据,每页10条,共3页; 则totalPage=3)
						let totalPage = data.pages;
		
						//设置列表数据
						if (page.num == 1) this.data_list = []; //如果是第一页需手动置空列表
						this.data_list=this.data_list.concat(curPageData)
						// 请求成功,隐藏加载状态
						//方法一(推荐): 后台接口有返回列表的总页数 totalPage
						this.mescroll.endByPage(curPageLen, totalPage);
				})
			}
		
		
		}
		
	}
</script>

<style lang="scss">
	.box {
		width: 100%;
		min-height: calc(100vh - var(--window-bottom) - var(--window-top));
		background: white;
	}
	.address-li {
		width: calc(100% - 80rpx);
		margin: 0 40rpx;
		display: flex;
		justify-content: space-between;
		align-items: center;
		position: relative;
		padding:30rpx 0;
		&::after {
			content: "";
			position: absolute;
			left: -40rpx;
			bottom: 0;
			width: calc(100% + 80rpx) ;
			height: 1px;
			background: #D5D5D5;
			z-index: 1;
		}
		.iconfont {
			font-size: 40rpx;
		}
		.address-box {
			width: 500rpx;
			flex-shrink: 0;
			font-size:30rpx;
			.title {
				max-height: 80rpx;
				overflow:hidden;
				text-overflow: ellipsis;
				display: -webkit-box;
				-webkit-line-clamp: 2; 
				-webkit-box-orient: vertical;
				.default {
					background:$color-orange;
					border-radius:6rpx;
					color: white;
					font-size: 22rpx;
					padding: 7rpx 6rpx;
					text-align: center;
					margin-right: 8rpx;
				}
			}
			.name-box {
				display: flex;
				justify-content: space-between;
				margin: 24rpx 0;
				margin-bottom: 0;
			}
		}
	}
.iconxuanzhong {
	color: $color-orange !important;
}
.bg-btn {
	width: 100%;
}
</style>
